export default class Text {
  constructor(parentMC, resourceManager, charId, ObjectId, placeAfter, transform, bounds) {
    this.parentEl = parentMC.el
    this.bounds = bounds

    this.el = this.parentEl.g()
    this.id = ObjectId
    this.el.attr({ 'class': 'text', 'token': this.id })

    this.children = []
    this.isMask = false
    this.isMasked = false
    this.mask = null
    this.maskTill = null

    for (let j = 0; j < resourceManager.m_data.DOMDocument.Text.length; j++)
    {
      if (resourceManager.m_data.DOMDocument.Text[j].charid == charId)
      {
        this.addText(resourceManager.m_data.DOMDocument.Text[j])
      }
    }

    const transformArray = transform.split(",")
    const transformMat = new window.Snap.Matrix(...transformArray)
    this.el.transform(transformMat)

    if (placeAfter && parseInt(placeAfter, 10) !== 0) {
      const afterMC = parentMC.getChildById(parseInt(placeAfter, 10))

      if (afterMC.isMasked) {  //if masked add outside mask group
        afterMC.el.parent().before(this.el)
      } else {
        afterMC.el.before(this.el)
      }
    } else {
      this.parentEl.add(this.el)
    }
  }
  addText(data) {
    var text,
      textBox,
      textX,
      textY,
      lineMode,
      fontSize,
      fontName,
      fontColor,
      textAlign,
      textAnchor,
      textBaseline,
      textBounds,
      textStyles

    textBox = this.el.g()

    if (this.bounds) {
      textBounds = this.bounds.split(',')
    } else {
      textBounds = [0, 0, 200, 100]
    }

    lineMode = data.behaviour.lineMode
    textAlign = data.paras[0].alignment
    textBaseline = lineMode == 'single' ? 'central' : 'auto'
    fontSize = data.paras[0].textRun[0].style.fontSize
    fontName = data.paras[0].textRun[0].style.fontName
    fontColor = data.paras[0].textRun[0].style.fontColor
    const letterSpacing = data.paras[0].textRun[0].style.letterSpacing

    if (textAlign == 'left') {
      textAnchor = 'start'
    } else if (textAlign == 'center') {
      textAnchor = 'middle'
    } else if (textAlign == 'right') {
      textAnchor = 'end'
    }

    textStyles = {
      'text-anchor': textAnchor,
      'dominant-baseline': textBaseline,
      'font-family': fontName,
      'font-size': fontSize,
      'letter-spacing': letterSpacing,
      'fill': fontColor,
    }

    //rect
    if (data.behaviour.isBorderDrawn !== "false") {
      const textRect = textBox.rect(textBounds[0], textBounds[1], textBounds[2], textBounds[3])
      textRect.attr({
        stroke: 'black',
        fill: 'transparent',
      })
    }

    //make text and y
    if (lineMode == 'single') {
      text = textBox.text(0, 0, data.txt)
      textY = parseFloat(textBounds[1]) + (parseFloat(textBounds[3]) / 2)
    } else {
      text = this.multiline(textBox, data, textBounds, textStyles)
      textY = parseFloat(textBounds[1]) - (parseFloat(data.paras[0].linespacing) * 2)
    }

    //x
    if (textAlign == 'left') {
      textX = parseFloat(textBounds[0])
    } else {
      textX = parseFloat(textBounds[0]) + (parseFloat(textBounds[2]) / 2)
    }

    text.attr(textStyles)
    text.transform('translate(' + textX + ',' + textY + ')')
  }
  multiline(textBox, data, textBounds, textStyles) {
    var spans = [],
      chars = '',
      substr,
      tempTxt,
      boundsWidth = parseFloat(textBounds[2]),
      sp,
      bbox,
      i = 0

    //break into spans
    while (i > -1) {
      chars += data.txt.charAt(i)
      tempTxt = textBox.text(0, 0, chars)
      tempTxt.attr(textStyles)

      bbox = tempTxt.getBBox()

      const newIndex = chars.lastIndexOf(' ')

      if (bbox.w > boundsWidth) {
        substr = chars.slice(0, newIndex)
        spans.push(substr)
        i = i - (chars.length - substr.length) + 2
        chars = ''
      } else {
        i += 1
      }

      if (i >= data.txt.length) {
        substr = chars.slice(0, newIndex)
        spans.push(substr)
        i = -1
      }

      tempTxt.remove()
    }

    const text = textBox.text(0, 0, spans)
    sp = text.selectAll('tspan')
    sp.attr({
      'x': 0,
      'dy': bbox.h + parseFloat(data.paras[0].linespacing),
    })

    return text
  }
}