import GP from './garbagePool'
import CMD from './commands'
import ResourceManager from './resourceManager' // eslint-disable-line

/**
 * @implements {_snapsvgAnimator.MovieClip}
 */
export default class MovieClip {
  /**
   * @param {_snapsvgAnimator.Timeline} commandTimeline
   * @param {_snap.Paper|!MovieClip} s
   * @param {ResourceManager} resourceManager
   * @param {string} objectID
   * @param {string} [name]
   * @param {string} [transform]
   */
  constructor(commandTimeline, s, resourceManager, objectID, name, transform) {
    /**
     * @type {_snap.Paper}
     */
    const parentEl = s.type == 'svg' ? s : s.el  // parent is stage if svg

    if (objectID) this.id = objectID

    if (name) this.name = name

    this.el = parentEl.g()
    this.el.attr({ 'class': 'movieclip', 'token': this.id })
    this.transform = transform

    this.m_timeline = commandTimeline
    this.m_currentFrameNo = 0
    this.m_frameCount = this.m_timeline.frameCount

    this._scripts = {}
    this._labels = []
    /**
     * @type {!Array<!MovieClip>}
     */
    this.children = []
    this.isMask = false
    this.isMasked = false
    this.mask = null
    this.maskElement = null
    this.maskTill = null
    this.loops = true
    this.playing = true
    this.resourceManager = resourceManager
    this.commandList = []
    /**
     * @type {_snap.Matrix}
     */
    this.matrix = new window.Snap.Matrix()

    if (this.m_timeline.Label) {
      this._labels = this.m_timeline.Label
    }

    if (this.transform) {
      const transformArray = this.transform.split(',')
      this.matrix = new window.Snap.Matrix(...transformArray)
      this.el.transform(this.matrix)
    }
  }
  addChild(child, placeAfter) {
    placeAfter = placeAfter ? placeAfter : 0
    this.insertAtIndex(child, placeAfter)
  }
  /**
   * @param {!MovieClip} child
   * @param {string} [placeAfter]
   */
  _addChild(child, placeAfter) {
    if (child.name) {
      /**
       * @suppress {checkTypes}
       */
      this[child.name] = child
    }

    if (placeAfter && parseInt(placeAfter, 10) !== 0) {
      const afterMC = this.getChildById(parseInt(placeAfter, 10))

      if (afterMC.isMasked) {  //if masked add outside mask group
        afterMC.el.parent().before(child.el)
      } else {
        afterMC.el.before(child.el)
      }
    } else {
      this.el.add(child.el)
    }
  }
  //manage children methods
  /**
   * @param {number| string} id
   */
  getChildById(id) {
    return this.children.find(({ id: i }) => {
      return i == id
    })
  }
  /**
   * @param {number} id
   */
  getChildIndexById(id) {
    return this.children.findIndex(({ id: i }) => {
      return i == id
    })
  }
  /**
   * @param {number} id
   */
  removeChildById(id) {
    for (let i = 0; i < this.children.length; i += 1) {
      if (this.children[i].id == id) {
        this.children.splice(i, 1)
        return
      }
    }
  }
  /**
   * @param {number} id
   * @param {number} placeAfter
   */
  swapChildIndex(id, placeAfter) {
    var i, child

    for (i = 0; i < this.children.length; i += 1) {
      if (this.children[i].id == id) {
        child = this.children.splice(i, 1)
        break
      }
    }

    for (i = 0; i < this.children.length; i += 1) {
      if (this.children[i].id == placeAfter) {
        this.children.splice(i + 1, 0, child[0])
        break
      }
    }
  }
  insertAtIndex(child, placeAfter) {
    var i

    this._addChild(child, placeAfter)

    if (parseInt(placeAfter, 10) === 0) {
      this.children.unshift(child)
    }

    for (i = 0; i < this.children.length; i += 1) {
      if (this.children[i].id == placeAfter) {
        this.children.splice(i + 1, 0, child)
        break
      }
    }
  }
  containsMask() {
    return this.children.some(({ isMask }) => isMask)
  }
  getFrameLabels() { return this._labels }
  getMatrix() {
    //TODO:: set to newM if exists
    if (this.matrix) {
      return this.matrix
    } else {
      return new window.Snap.Matrix()
    }
  }
  getX() {
    let _x = 0

    if (this.matrix) {
      _x = this.matrix.x()
    }

    return _x
  }
  getY() {
    let _y = 0

    if (this.matrix) {
      _y = this.matrix.y()
    }

    return _y
  }
  mouseover(cb) {
    this.el.mouseover(cb)
  }
  mouseout(cb) {
    this.el.mouseout(cb)
  }
  mousedown(cb) {
    this.el.mousedown(cb)
  }
  mousemove(cb) {
    this.el.mousemove(cb)
  }
  click(cb) {
    this.el.click(cb)
  }
  executeFrameScript(script) {
    eval('(function () {' + script + '}).call(this);')
  }
  removeFrameScript(id) {
    delete this._scripts[id]
  }
  addFrameScript(id, script) {
    this._scripts[id] = script
  }
  getFrame(num) {
    return this.m_timeline.Frame.find(({ num: n }) => n == num)
  }
  _checkLoop() {
    if (this.m_currentFrameNo == this.m_frameCount) {
      if (!this.loops) return
      this._loop()
    }
  }
  _loop() {
    var frame,
      commands,
      i,
      found,
      type

    this.m_currentFrameNo = 0

    frame = this.getFrame(this.m_currentFrameNo)

    if (!frame) {
      this.clearChildren()
      return
    }

    //Get the commands for the first frame
    commands = frame.Command

    // Iterate through all the elements in the display list
    // check if same instance exists in the first frame
    for (i = 0; i < this.children.length; i += 1) {
      found = false
      const child = this.children[i]

      for (let c = 0; c < commands.length; ++c) {
        const cmdData = commands[c]
        type = cmdData.cmdType

        if (type == 'Place') {
          if (parseInt(child.id, 10) == parseInt(cmdData.objectId, 10)) {
            found = true
            break
          }
        }
      }

      if (found === false) {
        const command = new CMD.RemoveObjectCommand(child.id)
        this.commandList.push(command)
      }
    }
  }
  clearChildren() {
    var i,
      child,
      command

    for (i = 0; i < this.children.length; i += 1) {
      child = this.children[i]
      command = new CMD.RemoveObjectCommand(child.id)
      this.commandList.push(command)
    }
  }
  _animate() {
    var i

    if (this.playing !== false) {
      this.step_1_animTimeline()
      this.step_2_enterFrame()
      //this.step_3_addPending();
      this.step_4_frameConstructed()
      this.step_5_frameScripts()
      this.step_6_exitFrame()
    }

    for(i = 0; i < this.children.length; i += 1)
    {
      if (this.children[i]._animate) {
        this.children[i]._animate()
      }
    }

    GP.purge()
  }
  /**
   * @param {Array<_snapsvgAnimator.Command>} commands
   */
  _runCommands(commands) {
    let command
    for (let c = 0; c < commands.length; c += 1) {
      const cmdData = commands[c]
      const type = cmdData.cmdType
      command = null
      let found

      switch(type) {
      case 'Place':

        found = this.getChildById(cmdData.objectId)

        if (!found) {
          command = new CMD.PlaceObjectCommand(cmdData.charid, cmdData.objectId, cmdData.name, cmdData.placeAfter, cmdData.transformMatrix, cmdData.bounds)
          this.commandList.push(command)
        } else {
          command = new CMD.MoveObjectCommand(cmdData.objectId, cmdData.transformMatrix)
          this.commandList.push(command)
          command = new CMD.UpdateObjectCommand(cmdData.objectId, cmdData.placeAfter)
          this.commandList.push(command)
        }

        break
      case 'Move':
        command = new CMD.MoveObjectCommand(cmdData.objectId, cmdData.transformMatrix)
        this.commandList.push(command)
        break
      case 'Remove':
        command = new CMD.RemoveObjectCommand(cmdData.objectId)
        this.commandList.push(command)
        break
      case 'UpdateZOrder':
        command = new CMD.UpdateObjectCommand(cmdData.objectId , cmdData.placeAfter)
        this.commandList.push(command)
        break
      case 'UpdateVisibility':
        command = new CMD.UpdateVisibilityCommand(cmdData.objectId , cmdData.visibility)
        this.commandList.push(command)
        break
      case 'UpdateColorTransform':
        command = new CMD.UpdateColorTransformCommand(cmdData.objectId , cmdData.colorMatrix)
        this.commandList.push(command)
        break
      case 'UpdateBlendMode':
        //  command = new CMD.UpdateBlendMode(cmdData.objectId , cmdData.blendMode);
        //  this.commandList.push(command);
        break
      case 'UpdateMask':
        command = new CMD.UpdateMaskCommand(cmdData.objectId , cmdData.maskTill)
        this.commandList.push(command)
        break
      case 'AddFrameScript':
        command = new CMD.AddFrameScriptCommand(cmdData.scriptId, cmdData.script)
        this.commandList.push(command)
        break
      case 'RemoveFrameScript':
        command = new CMD.RemoveFrameScriptCommand(cmdData.scriptId)
        this.commandList.push(command)
        break
      case 'SetFrameLabel':
        command = new CMD.SetFrameLabelCommand(cmdData.Name)
        this.commandList.push(command)
        break
      }
    }

    if (this.containsMask) {
      command = new CMD.ApplyMaskCommand()
      this.commandList.push(command)
    }

    this.executeCommands(this.commandList, this.resourceManager)
  }
  //update timeline animations
  step_1_animTimeline() {
    if (!this.playing) {
      return
    }

    this.commandList = []

    this._checkLoop()

    const frame = this.getFrame(this.m_currentFrameNo)
    this.m_currentFrameNo++

    if (!frame) {
      return
    }

    const commands = frame.Command
    this._runCommands(commands)
  }
  step_2_enterFrame() {
    //dispatch enter frame event
    //trigger on children
  }
  step_3_addPending() {

  }
  step_4_frameConstructed() {
    //dispatch frame constructed event
    //trigger on children
  }
  step_5_frameScripts() {
    //trigger framescripts
    //trigger on children

    for (var i in this._scripts) {
      this.executeFrameScript.call(this, this._scripts[i])
    }
  }
  step_6_exitFrame() {
    //dispatch exit frame event
    //trigger on children
  }
  play() {
    this.playing = true
  }
  stop() {
    this.playing = false
  }
  gotoAndStop(fr) {
    this._gotoAndPlayStop(fr, true)
  }
  gotoAndPlay(fr) {
    this._gotoAndPlayStop(fr, false)
  }
  _gotoAndPlayStop(frame, bStop) {
    //TODO::handle labels
    if (typeof frame === 'string') {
      var labels = this.getFrameLabels()
      var bFound = false

      for (var i = labels.length - 1; i >= 0; i--) {
        if (frame === labels[i].name) {
          frame = parseInt(labels[i].frameNum, 10) + 1
          bFound = true
          break
        }
      }

      if (bFound === false) {
        return
      }
    }

    //if frame number is invalid, don't do anything
    if (frame < 1 || frame > this.m_frameCount) {
      return
    }

    // If we are already at the destination frame, don't do anythin
    if (frame == this.m_currentFrameNo) {
      if (bStop === false) {
        this.play()
      } else {
        this.stop()
      }
      return
    }

    // First ensure that the timeline is in a good state to start jumping around
    //this.step_3_addPending(true);
    this.play()

    // Loop around if necessary
    if (frame < this.m_currentFrameNo) {
      var bSeekEnd = (frame == 1)
      this._loopAround()
      //this.step_3_addPending(!bSeekEnd);
    }

    while (this.m_currentFrameNo < frame) {
      // var bSeekEnd1 = (frame == this.m_currentFrameNo)
      this.step_1_animTimeline()

      for(var j = 0; j < this.children.length; j += 1)
      {
        if (this.children[j].step_1_animTimeline) {
          this.children[j].step_1_animTimeline()
        }
      }

      //this.step_3_addPending(!bSeekEnd);
    }

    if (bStop === false) {
      this.play()
    } else {
      this.stop()
    }

    this.step_4_frameConstructed()
    this.step_5_frameScripts()
    this.step_6_exitFrame()
  }
  _loopAround () {
    this.commandList = []
    this._loop()
    this.m_currentFrameNo = 0

    const frame = this.getFrame(this.m_currentFrameNo)

    if (!frame) {
      return
    }

    //Get the commands for the first frame
    const commands = frame.Command
    this._runCommands(commands)
  }
  executeCommands(commandList, resourceManager) {
    var i
    for (i = 0; i < commandList.length ; i++)
    {
      if (commandList[i] !== undefined) {
        commandList[i].execute(this, resourceManager)
      }
    }
  }
  log() {
    if (this.id.indexOf('svg') > -1) { // only on main timeline
      var args = Array.prototype.slice.call(arguments)
      args.unshift(this.id.toUpperCase())
      //console.log.apply(console, args);
    }
  }
}

