import MovieClip from './movieclip'
import ResourceManager from './resourceManager'

export default class SVGAnim {
  /**
   * SVGAnim
   * initialize animation component
   * @param {_snapsvgAnimator.Data} data The data type.
   * @param {DOMDocument} data.DOMDocument The DOMDocument.
   */
  constructor(data, w = 100, h = 100, fps = 24, params = {}) {
    this.fps = fps
    this.cbk = null
    this.data = data
    // color = '#008460',

    this.version = '1.2.3'
    // console.log('%c' + msg, 'color:' + color + ';font-weight:bold;');

    const { elementId, element, autoplay = true } = params

    this.playing = autoplay
    this.debug = false

    if (this.debug) {
      console.log('%c' + '/*DEBUG*/', 'color:#FF6666;font-weight:bold;')
      this.playing = false
      this.autoplay = false
      window.addEventListener('keydown', this.handleKeyDown)
    }

    this.resourceManager = new ResourceManager(data)

    //TODO:: RENDERER
    /**
     * @type {_snap.Paper}
     */
    const s = element ? new window.Snap(element) : elementId ? new window.Snap(elementId) : new window.Snap(w, h)
    s.attr({ 'id': s.id })
    s.attr({
      'viewBox': '0 0 ' + w + ' ' + h,
      'preserveAspectRatio': 'xMidYMid meet',
    })
    this.s = s
    this.id = s.id
    //TODO:: set bg color here

    //Set width and height of element if element id given
    if (elementId) {
      s.attr({
        'width': w,
        'height': h,
      })
    }

    this.mc = null
    this.create(s)

    if (this.debug) {
      setInterval(() => traceDisplayList(this.mc), 100)
    }

    if (autoplay) {
      this.play()
    } else {
      this.interval()
    }
    this.rootAnimator = undefined

    //generate linked movieclips
    this.linkage = {}
  }
  toString() {
    return 'Snap.svg Animator v' + this.version
  }

  /**
   * @param {_snap.Paper} s
   */
  create(s) {
    var maintimelineIndex

    if (this.rootAnimator) {
      this.rootAnimator.dispose()
    }

    for (let i = this.data.DOMDocument.Timeline.length - 1; i > -1; i -= 1) {
      if (this.data.DOMDocument.Timeline[i].linkageName) {
        this.linkage[this.data.DOMDocument.Timeline[i].linkageName] = this.data.DOMDocument.Timeline[i]
      } else {
        maintimelineIndex = i
        break
      }
    }

    const mainTimeline = this.resourceManager.m_data.DOMDocument.Timeline[maintimelineIndex]
    this.mc = new MovieClip(mainTimeline, s, this.resourceManager, this.id)

    this.cbk = setTimeout(() => {
      this.interval()
    }, 1000 / this.fps)
  }
  play() {
    this.mc.play()
    this.playing = true
  }
  stop() {
    if (this.mc.m_currentFrameNo == 0) { //force render if called immediately
      this.mc._animate()
    }

    this.mc.stop()
    this.playing = false
  }
  setLoop(l) {
    this.mc.loops = l
  }
  interval() {
    if (this.playing) {
      this.mc._animate()
    }
    clearTimeout(this.cbk)
    this.cbk = setTimeout(() => {
      this.interval()
    }, 1000 / this.fps)
  }
  handleKeyDown(e) {
    switch (e.keyCode) {
    case 65: //A inc frame by 1
      this.interval()
      break
    case 32: //SPACE toggles playing
      if (this.mc.playing) {
        this.stop()
      } else {
        this.play()
      }
      break
    }
  }
}

/**
 * Debug output for displaylist
 * @param {MovieClip} mc
 */
function traceDisplayList(mc) {
  var debug = document.getElementById('debug'),
    str = ''

  if (!debug) {
    debug = document.createElement('div')
    debug.id = 'debug'
    debug.style.position = 'absolute'
    debug.style.top = '0'
    debug.style.right = '0'
    debug.style.backgroundColor = 'black'
    debug.style.color = 'white'
    debug.style.padding = '1em'

    document.body.appendChild(debug)
  }

  function traceChildren(j, el) {
    var k, i

    for (i = 0; i < el.children.length; i += 1) {
      for (k = 0; k < j; k += 1) {
        str += '-'
      }
      str += el.children[i].id + ':' + el.children[i].children.length

      if (el.children[i].isMask) {
        str += ' (MASK till:' + el.children[i].maskTill + ')'
      }

      if (el.children[i].isMasked) {
        str += ' (masked by: ' + el.children[i].mask + ')'
      }

      str += '<br/>'
      traceChildren(j + 5, el.children[i])
    }
  }

  str += mc.id + '<br/>'
  str += mc.m_currentFrameNo + '<br/>'
  traceChildren(2, mc)

  debug.innerHTML = str
}

