import MovieClip from './movieclip' // eslint-disable-line
import ResourceManager from './resourceManager' // eslint-disable-line

export default class Shape {
  /**
   * @param {!MovieClip} parentMC
   * @param {!ResourceManager} resourceManager
   * @param {string} charId
   * @param {string} ObjectId
   * @param {string} placeAfter
   * @param {string} transform
   */
  constructor(parentMC, resourceManager, charId, ObjectId, placeAfter, transform) {
    const parentEl = parentMC.el
    this.parentEl = parentEl

    this.el = parentMC.el.g()
    this.id = ObjectId
    this.el.attr({ 'class': 'shape', 'token': this.id })

    this.children = []
    this.isMask = false
    this.isMasked = false
    this.mask = null
    this.maskTill = null

    this.resourceManager = resourceManager

    for (let j = 0; j < resourceManager.m_data.DOMDocument.Shape.length; j++)
    {
      if (resourceManager.m_data.DOMDocument.Shape[j].charid == charId)
      {
        for (let k = 0; k < resourceManager.m_data.DOMDocument.Shape[j].path.length; k++)
        {
          this.addPath(j, k)
        }
      }
    }

    const transformArray = transform.split(",")
    const transformMat = new window.Snap.Matrix(...transformArray)
    this.el.transform(transformMat)

    if (placeAfter && parseInt(placeAfter, 10) !== 0) {
      const afterMC = parentMC.getChildById(parseInt(placeAfter, 10))

      if (afterMC.isMasked) {  //if masked add outside mask group
        afterMC.el.parent().before(this.el)
      } else {
        afterMC.el.before(this.el)
      }
    } else {
      parentEl.add(this.el) //TODO:: handle after
    }
  }
  /**
   * @param {number} j
   * @param {number} k
   */
  addPath(j, k) {
    const shape = this.el.path()
    const resourcePath = this.resourceManager.m_data.DOMDocument.Shape[j].path[k]
    const path = resourcePath.d
    shape.attr({
      'fill': 'transparent',
      'd': path,
    })

    if(resourcePath.pathType == "Fill") {
      this.addFill(shape, resourcePath)
    } else if (resourcePath.pathType == "Stroke") {
      addStroke(shape, resourcePath)
    }
  }
  /**
   * @param {_snapsvgAnimator.ResourceImage} resourceImg
   */
  getFillImage(resourceImg) {
    const patternArray = resourceImg.patternTransform.split(",")
    const mat = new window.Snap.Matrix(...patternArray)

    const src = resourceImg.bitmapPath

    const exists = this.parentEl.paper.select('defs pattern image')

    let fillImage
    if (exists) {
      if (exists.attr('href') == src) {	//check if image href matches as well as other props
        fillImage = exists.parent()
      } else {
        fillImage = this.newPattern(src, resourceImg, mat)
      }
    } else {
      fillImage = this.newPattern(src, resourceImg, mat)
    }

    return fillImage
  }
  /**
   * @param {_snapsvgAnimator.Gradient} grad
   * @param {string} type
   * @param {_snap.Paper} [shape]
   */
  getFillGradient(grad, type, shape) {
    let gradientString
    if (type == 'linear') {
      const _x = parseFloat(grad.x1)
      const _y = parseFloat(grad.y1)
      const _x2 = parseFloat(grad.x2)
      const _y2 = parseFloat(grad.y2)
      gradientString = "L("
      gradientString += _x + ", "
      gradientString += _y + ", "
      gradientString += _x2 + ", "
      gradientString += _y2 + ")"
    } else {
      const _x = (shape.getBBox().x + shape.getBBox().width / 2) + grad.cx / 10
      const _y = (shape.getBBox().y + shape.getBBox().height / 2) + grad.cy / 10
      const _fx = (shape.getBBox().x + shape.getBBox().width / 2) + parseFloat(grad.fx)
      const _fy = (shape.getBBox().y + shape.getBBox().height / 2) + parseFloat(grad.fy)
      gradientString = "R("
      gradientString += _x + ", "
      gradientString += _y + ", "
      gradientString += grad.r + ", "
      gradientString += _fx + ", "
      gradientString += _fy + ")"
    }

    for (let i = 0; i < grad.stop.length; i += 1) {
      const { r, g, b } = hexToRgb(grad.stop[i].stopColor)
      gradientString += 'rgba(' + r + ',' + g + ',' + b + ',' + grad.stop[i].stopOpacity + ')'
      gradientString += ":"
      gradientString += grad.stop[i].offset
      if (i !== grad.stop.length-1) {
        gradientString += "-"
      }
    }

    const gradientFill = this.parentEl.paper.gradient(gradientString)
    return gradientFill
  }
  /**
   * @param {string} src
   * @param {_snapsvgAnimator.ResourceImage} resourceImg
   * @param {_snap.Matrix} mat
   */
  newPattern(src, resourceImg, mat) {
    const image = this.el.image(src)
    const pattern = image.pattern(0, 0, resourceImg.width, resourceImg.height)
    pattern.attr({ 'x': mat.e, 'y': mat.f })

    return pattern
  }
  /**
   * @param {_snap.Paper} shape
   * @param {_snapsvgAnimator.ResourcePath} resourcePath
   */
  addFill(shape, resourcePath) {
    var fillColor,
      fillImage,
      fillGradient,
      img,
      grad

    if (resourcePath.color) {
      fillColor = getFillColor(resourcePath)
      shape.attr({ 'fill': fillColor })
    }
    if (resourcePath.image) {
      img = resourcePath.image
      fillImage = this.getFillImage(img)
      shape.attr({ 'fill': fillImage })
    }
    if (resourcePath.linearGradient) {
      grad = resourcePath.linearGradient
      fillGradient = this.getFillGradient(grad, 'linear')
      shape.attr({ 'fill': fillGradient })
    }
    if (resourcePath.radialGradient) {
      grad = resourcePath.radialGradient
      fillGradient = this.getFillGradient(grad, 'radial', shape)
      shape.attr({ 'fill': fillGradient })
    }
  }
}

/**
 * @param {_snap.Paper} shape
 * @param {!Array<_snapsvgAnimator.ResourcePath>} shape.path The paths.
 * @param {_snapsvgAnimator.ResourcePath} resourcePath
 */
function addStroke(shape, resourcePath) {
  if (resourcePath.color) {
    const clr = resourcePath.color

    const r = parseInt(clr.substring(1, 3), 16)
    const g = parseInt(clr.substring(3, 5), 16)
    const b = parseInt(clr.substring(5, 7), 16)
    const a = resourcePath.colorOpacity

    const colStr = 'rgba(' + r + ',' + g + ',' + b + ',' + a + ')'

    shape.attr({ 'stroke': colStr, 'strokeWidth': resourcePath.strokeWidth })
  }
}

/**
 * @param {_snapsvgAnimator.ResourcePath} resourcePath
 */
function getFillColor(resourcePath) {
  const clr = resourcePath.color
  const r = parseInt(clr.substring(1, 3), 16)
  const g = parseInt(clr.substring(3, 5), 16)
  const b = parseInt(clr.substring(5, 7), 16)
  const a = resourcePath.colorOpacity
  const colStr = 'rgba(' + r + ',' + g + ',' + b + ',' + a + ')'

  return colStr
}

function hexToRgb(hex) {
  const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex)
  return result ? {
    r: parseInt(result[1], 16),
    g: parseInt(result[2], 16),
    b: parseInt(result[3], 16),
  } : null
}

